.btn-reset {
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

a:focus-visible {
  outline: none;
  box-shadow: 0 0 30px 4px #b8ffec;
}

.btn:focus-visible {
  outline: none;
  box-shadow: 0 0 3px 3px #ff3e79;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.stop-scroll {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "TT Lakes";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("TT Lakes Regular"), local("TT-Lakes-Regular"), url(./fonts/TTLakes-Regular.woff2) format("woff2"), url(./fonts/TTLakes-Regular.woff) format("woff");
}
@font-face {
  font-family: "TT Lakes";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("TT Lakes Medium"), local("TT-Lakes-Medium"), url(./fonts/TTLakes-Medium.woff2) format("woff2"), url(./fonts/TTLakes-Medium.woff) format("woff");
}
@font-face {
  font-family: "TT Lakes";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("TT Lakes Bold"), local("TT-Lakes-Bold"), url(./fonts/TTLakesCondensed-Bold.woff2) format("woff2"), url(./fonts/TTLakesCondensed-Bold.woff) format("woff");
}
.button {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background: none;
  display: block;
  transition-duration: 0.5s;
  cursor: pointer;
}

.side-menu {
  position: absolute;
  top: 0;
  width: 100%;
  max-width: 320px;
  height: 100%;
  z-index: 20;
  background: #ffffff;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
  transform: translateX(-120%);
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .side-menu {
    width: 320px;
  }
}
@media (min-width: 1440px) {
  .side-menu {
    transform: none;
    height: 100vh;
    display: block;
    box-shadow: none;
    position: relative;
    grid-area: side-menu;
  }
}

.side-menu--open {
  transform: none;
}

.side-menu__wrapper {
  background: #ffffff;
  width: 100%;
  height: 100%;
  padding-top: 22px;
  padding-bottom: 22px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .side-menu__wrapper {
    width: 320px;
  }
}
@media (min-width: 1440px) {
  .side-menu__wrapper {
    top: 0;
    position: fixed;
  }
}

.side-menu__header {
  max-width: 320px;
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 46px;
  padding-right: 24px;
  padding-left: 24px;
}

.side-menu__contacts {
  margin-top: auto;
  padding-right: 24px;
  padding-left: 24px;
}
@media (min-width: 768px) {
  .side-menu__contacts {
    margin-bottom: 40px;
  }
}

.side-menu__change-language {
  padding-right: 24px;
  padding-left: 24px;
}

.header-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ff3e79;
  background-repeat: no-repeat;
  background-position: center center;
}

.header-button--burger-menu {
  background-image: url(./static/button-icon--burger-menu.svg);
  background-size: 18px 16px;
}
@media (min-width: 768px) {
  .header-button--burger-menu {
    position: relative;
    margin-right: 22px;
  }
}
@media (min-width: 1440px) {
  .header-button--burger-menu {
    display: none;
  }
}

@media (min-width: 768px) {
  .header-button--burger-menu::before {
    position: absolute;
    content: "";
    width: 2px;
    height: 32px;
    top: 50%;
    right: -20px;
    transform: translateY(-50%);
    border-radius: 1px;
    background: #eaeaea;
  }
}

.header-button--close-menu {
  background-image: url(./static/button-icon--close-menu.svg);
  background-size: 16px 16px;
}
@media (min-width: 1440px) {
  .header-button--close-menu {
    display: none;
  }
}

.header-button--search {
  background-image: url(./static/button-icon--search.svg);
  background-size: 17px 17px;
  margin-left: auto;
}

.header-button--hide {
  display: none;
}

.header-button--phone {
  background-image: url(./static/button-icon--phone.svg);
  background-size: 18px 18px;
}

.header-button--chat {
  background-image: url(./static/button-icon--chat.svg);
  background-size: 30px 30px;
}

.header-button--profile {
  background-image: url(./static/button-icon--profile.svg);
  background-size: 20px 18px;
}

.header-button--repair-order {
  position: relative;
  background-image: url(./static/button-icon--repair-order.svg);
  background-size: 20px 18px;
}

.header-button--repair-order::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 32px;
  top: 50%;
  right: 60px;
  transform: translateY(-50%);
  border-radius: 1px;
  background: #eaeaea;
}

.header-button--check-status {
  background-image: url(./static/button-icon--check-status.svg);
  background-size: 20px 18px;
}

.header-button:focus,
.header-button:hover {
  opacity: 0.7;
}

.header-button:active {
  opacity: 0.9;
}

.nav-list {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.nav-list__item {
  position: relative;
  padding-right: 24px;
  padding-left: 24px;
}

.nav-list__item--current::before {
  width: 4px;
  height: 100%;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-image: url(./static/highlight-icon.svg);
}

.nav-list__link {
  display: block;
  color: #1b1c21;
  text-decoration: none;
}

.nav-list__link:focus,
.nav-list__link:hover {
  color: #ff3e79;
  text-decoration: underline;
}

.nav-list__link:active {
  text-decoration: none;
}

.change-language {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.change-language__link {
  position: relative;
  display: block;
  width: 24px;
  height: 24px;
  color: #1B1C21;
  font-family: "TT Lakes", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 150% */
  letter-spacing: -0.2px;
  text-transform: uppercase;
}

.change-language__link--russian::after {
  content: "ru";
}

.change-language__link--english::after {
  content: "en";
}

.change-language__link--chinese::after {
  content: "ch";
}

.change-language__link--current {
  color: #7E7E82;
}

.change-language__link:focus, .change-language__link:hover {
  box-shadow: 0 0 1px 1px;
}

.contacts {
  display: flex;
  flex-direction: column;
}

.contacts__list {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 14px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.contacts__mail {
  margin-bottom: 14px;
  color: #1B1C21;
  font-family: "TT Lakes", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px; /* 150% */
  letter-spacing: -0.2px;
  text-decoration: none;
  transition-duration: 0.5s;
}

.contacts__mail:focus, .contacts__mail:hover {
  text-decoration: underline;
  transform: scale(1.1) translateX(15%);
}

.contacts__mail:active {
  text-decoration: none;
}

.contacts__phone {
  display: block;
  width: -moz-max-content;
  width: max-content;
  color: #1B1C21;
  font-family: "TT Lakes", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px; /* 133.333% */
  letter-spacing: -0.6px;
  text-decoration: none;
  transition-duration: 0.5s;
}

.contacts__phone:focus, .contacts__phone:hover {
  color: #ff3e79;
  opacity: 0.7;
  box-shadow: 0 0 1px 2px #ff3e79;
}

.contacts__phone:active {
  opacity: 0.9;
}

.page-logo {
  transition-duration: 0.5s;
}

.page-logo:focus, .page-logo:hover {
  opacity: 0.7;
}

.page-logo:active {
  opacity: 0.9;
}

.page {
  box-sizing: border-box;
}

.page__body {
  min-width: 320px;
  font-family: "TT Lakes", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1b1c21;
  background-color: #f8f8f8;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

@media (min-width: 1440px) {
  .page__wrapper {
    max-width: 1440px;
    margin-right: auto;
    margin-left: auto;
    display: grid;
    grid-template-areas: "side-menu page-main" "side-menu page-footer";
  }
}

.page__main {
  padding-top: 22px;
  width: 100%;
}
@media (min-width: 768px) {
  .page__main {
    padding-top: 16px;
  }
}
@media (min-width: 1440px) {
  .page__main {
    padding-top: 42px;
    grid-area: page-main;
  }
}

.page__footer {
  grid-area: page-footer;
  padding-top: 45px;
  padding-right: 10px;
  padding-left: 10px;
}
@media (min-width: 1440px) {
  .page__footer {
    padding-left: 33px;
    padding-right: 33px;
  }
}

.page__menu-overlay {
  display: none;
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: #ffffff;
  opacity: 0.8;
}
@media (min-width: 1440px) {
  .page__menu-overlay {
    display: none;
  }
}

.page__menu-overlay--show {
  display: block;
}
@media (min-width: 1440px) {
  .page__menu-overlay--show {
    display: none;
  }
}

.page-header {
  background-color: #ffffff;
  border-bottom: 1px solid #d9fff5;
}
@media (min-width: 1440px) {
  .page-header {
    display: none;
  }
}

.page-header__wrapper {
  padding: 19px 16px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    padding: 19px 24px;
    gap: 18px;
  }
}

.page-header__logo {
  margin-right: auto;
}

.page-header__controls-group {
  display: none;
}
@media (min-width: 768px) {
  .page-header__controls-group {
    padding-right: 20px;
    display: flex;
    gap: 16px;
  }
}

.order-controls__link {
  display: flex;
  align-items: center;
  gap: 10px;
}

.order-controls__button-label {
  display: block;
  color: #1b1c21;
  text-align: right;
  font-family: "TT Lakes", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
}

.order-controls__button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ff3e79;
  background-repeat: no-repeat;
  background-position: center center;
}

.order-controls__button--repair-order {
  background-image: url(./static/button-icon--repair-order.svg);
  background-size: 20px 20px;
}

.order-controls__button--check-status {
  background-image: url(./static/button-icon--check-status.svg);
  background-size: 20px 20px;
}

.order-controls__button:focus,
.order-controls__button:hover {
  opacity: 0.7;
}

.order-controls__button:active {
  opacity: 0.9;
}

.about {
  border-bottom: 1px solid #d9fff5;
  padding-bottom: 16px;
  position: relative;
}

.about__order-controls {
  display: none;
}
@media (min-width: 1440px) {
  .about__order-controls {
    position: absolute;
    display: flex;
    gap: 35px;
    top: -3px;
    right: 27px;
  }
}

.about__title {
  position: relative;
  color: #1B1C21;
  font-family: "TT Lakes", "Arial", sans-serif;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 40px;
  letter-spacing: -0.6px;
  margin: 0;
  margin-bottom: 20px;
  display: block;
  padding-left: 18px;
  position: relative;
}
@media (min-width: 768px) {
  .about__title {
    margin-bottom: 33px;
    padding-left: 24px;
  }
}
@media (min-width: 1440px) {
  .about__title {
    margin-bottom: 8px;
    padding-left: 33px;
  }
}

.about__title::before {
  width: 4px;
  height: 40px;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-image: url(./static/highlight-icon.svg);
}

.about__page-nav {
  margin-bottom: 10px;
  padding: 0 16px;
}
@media (min-width: 768px) {
  .about__page-nav {
    margin-bottom: 15px;
    padding-left: 24px;
  }
}
@media (min-width: 1440px) {
  .about__page-nav {
    padding-left: 32px;
  }
}

.about__description {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
@media (min-width: 768px) {
  .about__description {
    flex-direction: row;
    gap: 5px;
    padding: 0 24px;
  }
}
@media (min-width: 1440px) {
  .about__description {
    padding-left: 33px;
  }
}

.about__container {
  padding-bottom: 10px;
  width: 100%;
}

.about__container:first-child {
  padding: 0 16px;
}
@media (min-width: 768px) {
  .about__container:first-child {
    padding: 0;
  }
}

.about__text-wrapper {
  margin-bottom: 16px;
  max-height: 90px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 768px) {
  .about__text-wrapper {
    width: 88%;
    max-height: 160px;
  }
}

.about__text-content {
  margin: 0;
